import React from "react";
import styled from "styled-components";

function Error() {
  return (
    <Wrapper>
      <h1>404</h1>
      <h2>Sorry we haven't made the page you're looking for yet^^ </h2>
    </Wrapper>
  );
}
const Wrapper = styled.div`
  width: 50%;
  margin: 5% auto;
  text-align: center;
  h1 {
    color: red;
  }
`;
export default Error;
