import React, { useContext, useState, useEffect } from "react";
import styled from "styled-components";
import MyContext from "../../../Context/Context";
import WeatherSummary from "./WeatherSummary";
import SingleInformation from "./SingleInformation";
import LineChart from "./LineChart";
function Informations() {
  const { loading, labels, list, setLabels, setDegrees, error } =
    useContext(MyContext);

  return (
    <Wrapper>
      {loading ? (
        <Centeral key="951541412237871">
          <h1>Loading...</h1>
        </Centeral>
      ) : (
        <LineChart />
      )}

      {!loading &&
        list.map((forecast, index) => {
          let { dt_txt } = list[index];
          const [date, clock] = dt_txt.split(" ");
          let weatherSummary = list[index].weather;

          return (
            <ForecastInfos key={index}>
              {weatherSummary.map(({ id, main, description }) => {
                let summaryValues = { date, clock, main };
                return (
                  <WeatherSummary
                    key={id}
                    values={summaryValues}
                  ></WeatherSummary>
                );
              })}
              <SingleInformation key={index} values={list[index]} />
            </ForecastInfos>
          );
        })}
    </Wrapper>
  );
}
const Wrapper = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
`;
const Centeral = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
`;
const ForecastInfos = styled.div`
  border: 1px solid #fff;
  display: flex;
  width: 80%;
  padding: 1%;
  flex-direction: column;
  margin: 1% auto;
  p {
    display: inline;
  }
  h4 {
    display: inline;
  }
`;
export default Informations;
