import React, { useContext } from "react";
import styled from "styled-components";
import MyContext from "../Context/Context";
import Informations from "./Components/Homepages/Informations";

function Homepage() {
  const { city, loading, error } = useContext(MyContext);
  return (
    <Wrapper>
      <div className="titleContainer">
        {!loading && <CityName>{city}</CityName>}
      </div>
      <div className="otherInfos">
        <Informations key="18411" />
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  margin: 2% auto;
  width: 90%;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 1%;
  .titleContainer {
    display: flex;
    width: 100%;
    justify-content: center;
    text-transform: capitalize;
  }
  .otherInfos {
    width: 100%;
    display: flex;
  }
`;
const CityName = styled.h3`
  font-size: 1.5rem;
`;
export default Homepage;
