import React, { createContext, useState, useEffect } from "react";
import axios from "axios";

const MyContext = createContext();

export const ContextProvider = ({ children }) => {
  const [city, setCity] = useState("denizli");
  const apiUrl = `https://api.openweathermap.org/data/2.5/forecast?q=${city}&cnt=10&appid=${process.env.REACT_APP_WEATHER_API_KEY}&units=metric`;
  //GET INFO
  const [data, setData] = useState([]);
  //GET SEARCH INFO
  const [searchWord, setSearchWord] = useState("");
  // CHANGE PAGE TITLE
  const [title, setTitle] = useState("Weather App Tyrcae");
  //LOADING PAGE
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  //All list items
  const [list, setList] = useState([]);

  //for graphic
  const [labels, setLabels] = useState([]);
  const [degrees, setDegrees] = useState([]);
  const [notice, setNotice] = useState(false);
  //functions
  const handleSearch = (e) => {
    e.preventDefault();
    setCity(searchWord);
  };

  const fetchData = async () => {
    const response = await axios.get(apiUrl).catch((err) => {
      setError(true);
      setCity("denizli");
      alert("Please enter a valid city");
      setSearchWord("");
    });
    setLoading(true);
    setData(response.data);
    setList(response.data.list);
    setNotice(!notice);
    setLoading(false);
    setSearchWord("");
  };
  useEffect(() => {
    setDegrees([]);
    setLabels([]);
    fetchData();
  }, [city]);

  const values = {
    data,
    title,
    city,
    setTitle,
    setCity,
    searchWord,
    setSearchWord,
    handleSearch,
    loading,
    list,
    labels,
    setLabels,
    degrees,
    setDegrees,
    notice,
    error,
    setError,
  };
  return <MyContext.Provider value={values}>{children}</MyContext.Provider>;
};

export default MyContext;
