import React, { useContext, useEffect } from "react";
import MyContext from "../../../Context/Context";

function SingleInformation({ values }) {
  const { data, notice, labels, setLabels, degrees, setDegrees } =
    useContext(MyContext);
  let { dt, dt_txt } = values;
  let { temp, feels_like, temp_min, temp_max, humidity } = values.main;
  let { speed, deg } = values.wind;
  useEffect(() => {
    setDegrees((prevItems) => [...prevItems, temp]);
    setLabels((prevItems) => [...prevItems, dt_txt]);
  }, [notice]);

  return (
    <div>
      <section>
        <p>Average Temparature : </p> <h4>{temp} C°</h4>
      </section>
      <section>
        <p>Feels Like Temparature : </p> <h4>{feels_like} C°</h4>
      </section>
      <section>
        <p>Expected Min Temparature : </p> <h4>{temp_min} C°</h4>
      </section>
      <section>
        <p>Expected Max Temparature : </p> <h4>{temp_max} C°</h4>
      </section>
      <section>
        <p>Humidity : </p> <h4>{humidity} %</h4>
      </section>
      <section>
        <p>Wind Speed : </p> <h4>{speed} </h4>
        <br />
        <p>Wind Degree : </p> <h4>{deg}°</h4>
      </section>
    </div>
  );
}

export default SingleInformation;
