import React, { useContext, useEffect } from "react";
import MyContext from "../../../Context/Context";

function WeatherSummary({ values }) {
  let { date, clock, main } = values;

  return (
    <div
      style={{
        width: "90%",
        margin: "1% auto",
        textAlign: "center",
      }}
    >
      <p>Date : </p> <h4>{date}</h4> <p> Clock :</p> <h4>{clock}</h4>
      <br />
      <p>Weather Summary : </p> <h4> {main}</h4>
    </div>
  );
}

export default WeatherSummary;
