import React, { useContext } from "react";
import styled from "styled-components";
import SearchIcon from "@mui/icons-material/Search";
import MyContext from "../../../../Context/Context";

function Form() {
  const { searchWord, setSearchWord, handleSearch } = useContext(MyContext);
  return (
    <Wrapper>
      <form className="searchForm" onSubmit={handleSearch}>
        <SearchIcon onClick={handleSearch} className="icon" />
        <input
          type="text"
          placeholder="Search with City name..."
          value={searchWord}
          onChange={(e) => setSearchWord(e.target.value)}
        />
      </form>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  .searchForm {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    padding: 0;
    position: relative;
    background: transparent;
    border: none;
    border-radius: 20px;
    padding: 0.5% 1%;
    min-width: 50%;
    &:hover {
      border: 1px solid #fff;
    }

    input {
      border: none;
      height: 2rem;
      width: 80%;
      min-width: 50%;
      font-size: 1rem;
      background: transparent;
      color: #fff;
      font-weight: 300;
      text-align: center;
      &:focus {
        outline: none;
        border: none;
      }
    }
    .icon {
      display: flex;
      position: absolute;
      font-size: 1.5rem;
      left: 0;
      margin-left: 5px;
      cursor: pointer;
    }
  }
`;

export default Form;
