import { Line } from "react-chartjs-2";
import React, { useContext } from "react";
import MyContext from "../../../Context/Context";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import styled from "styled-components";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);
function LineChart() {
  const { degrees, labels } = useContext(MyContext);
  return (
    <Wrapper>
      <Line
        data={{
          labels: [...labels],
          datasets: [
            {
              label: " Degree",
              data: [...degrees],
              borderColor: ["rgba(255,99,132,1)"],
              backgroundColor: "rgba(255, 99, 132, 0.5)",
            },
          ],
        }}
        height={200}
        width={300}
      />
    </Wrapper>
  );
}
const Wrapper = styled.div`
  width: 50%;
  margin: auto;
`;
export default LineChart;
